@font-face {
  font-family: 'NanumBarunGothic';
  font-style: normal;
  font-weight: 100;
  src: url('NanumBarunGothicUltraLight.eot'),
    /* IE9 Compat Modes */ url('NanumBarunGothicUltraLight.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('NanumBarunGothicUltraLight.woff2') format('woff2'),
    /* Modern Browsers */ url('NanumBarunGothicUltraLight.woff') format('woff'),
    /* Modern Browsers */ url('NanumBarunGothicUltraLight.ttf')
      format('truetype'); /* Safari, Android, iOS */
}

